<!--公用组件：
 /**
  * @desc Divisions
  * @module @/components
  * @desc 组件描述：
  * @author zkhq
  * @date 2020-03-23 14:33
  * @param {Object} [title]    - 参数说明
  * @param {String} [columns] - 参数说明
  * @example 调用示例
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
    <div>
      <el-cascader
        v-model="divisionValue"
        :props="props"
        @change="emitToParent"
        clearable>
      </el-cascader>
    </div>
</template>

<script>
// import
import {queryDivisions} from '@/api'
export default {
  name: 'Divisions',
  props: ['divisionValue'],
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      props: {
        checkStrictly: true, // 多选
        emitPath: true, // 只显示最后一级
        lazy: true,
        lazyLoad (node, resolve) {
          // 查询所需参数
          let param = null
          if (node.root) {
            param = {pcode: '86'}
          } else {
            param = {pcode: node.value}
          }
          queryDivisions(param).then((response) => {
            let nodes = []
            if (response.data.code === 0) {
              response.data.data.forEach(item => {
                let obj = {}
                obj.value = item.areaCode
                obj.label = item.areaName
                obj.leaf = node.level >= 3
                nodes.push(obj)
              })
            }
            resolve(nodes)
          })
        }
      },
      value: []
    }
  },
  components: {queryDivisions},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /** 向父类发送value */
    emitToParent: function () {
      this.$emit('sendDivisions', this.divisionValue)
    }
  }
}
</script>

<style scoped>

</style>



