<template>
    <div>
      <div class="app-container-item">
        <div class="app-form" v-show="showQuery">
          <div class="app-form-item">
            <el-input placeholder="输入用户名搜索" type="text" v-model="tableOptions.query.userName" style="width: 200px;"/>
          </div>
          <div class="app-form-buttons">
            <div class="app-form-buttons-item">
              <el-button @click="loadData" type="primary">
                <ant-icon icon-class="SearchOutlined"></ant-icon>
                查询
              </el-button>
            </div>
            <div class="app-form-buttons-item">
              <el-button @click="clear" type="success">
                <ant-icon icon-class="RedoOutlined"></ant-icon>
                重置
              </el-button>
            </div>
          </div>
        </div>
        <div class="app-hr" v-show="showQuery"></div>
        <div class="app-form">
          <div class="app-form-buttons">
            <div class="app-form-buttons-item" v-buttonshow="'upms:User:add'">
              <el-button @click="addUser" type="primary">
                <ant-icon icon-class="PlusOutlined"></ant-icon>
                新增
              </el-button>
            </div>
            <div class="app-form-buttons-item" v-buttonshow="'upms:User:upd'">
              <el-button @click="modifyUser" type="success">
                <ant-icon icon-class="EditOutlined"></ant-icon>
                修改
              </el-button>
            </div>
            <div class="app-form-buttons-item" v-buttonshow="'upms:User:del'">
              <el-button @click="removeUser" type="danger">
                <ant-icon icon-class="DeleteOutlined"></ant-icon>
                删除
              </el-button>
            </div>
            <div class="app-form-buttons-item" v-buttonshow="'upms:User:initPwd'">
              <el-button @click="initPwd" type="warning">
                <ant-icon icon-class="UploadOutlined"></ant-icon>
                初始密码
              </el-button>
            </div>
          </div>
          <el-button-group class="app-form-button-group align-right">
            <el-button
              plain
              type="info"
              icon="el-icon-search"
              @click="toggleQuery"
            />
            <el-button
              icon="el-icon-refresh"
              @click="refresh"
            />
<!--            <el-popover-->
<!--              placement="bottom-end"-->
<!--              width="150"-->
<!--              trigger="click"-->
<!--            >-->
<!--              <el-button-->
<!--                slot="reference"-->
<!--                icon="el-icon-s-grid"-->
<!--              >-->
<!--              </el-button>-->
<!--&lt;!&ndash;              <el-checkbox>1</el-checkbox>&ndash;&gt;-->
<!--&lt;!&ndash;              <el-checkbox>2</el-checkbox>&ndash;&gt;-->
<!--&lt;!&ndash;              <el-checkbox>3</el-checkbox>&ndash;&gt;-->
<!--&lt;!&ndash;              <el-checkbox&ndash;&gt;-->
<!--&lt;!&ndash;                v-model="allColumnsSelected"&ndash;&gt;-->
<!--&lt;!&ndash;                :indeterminate="allColumnsSelectedIndeterminate"&ndash;&gt;-->
<!--&lt;!&ndash;                @change="handleCheckAllChange"&ndash;&gt;-->
<!--&lt;!&ndash;              >&ndash;&gt;-->
<!--&lt;!&ndash;                全选&ndash;&gt;-->
<!--&lt;!&ndash;              </el-checkbox>&ndash;&gt;-->
<!--&lt;!&ndash;              <el-checkbox&ndash;&gt;-->
<!--&lt;!&ndash;                v-for="item in crud.props.tableColumns"&ndash;&gt;-->
<!--&lt;!&ndash;                :key="item.label"&ndash;&gt;-->
<!--&lt;!&ndash;                v-model="item.visible"&ndash;&gt;-->
<!--&lt;!&ndash;                @change="handleCheckedTableColumnsChange(item)"&ndash;&gt;-->
<!--&lt;!&ndash;              >&ndash;&gt;-->
<!--&lt;!&ndash;                {{ item.label }}&ndash;&gt;-->
<!--&lt;!&ndash;              </el-checkbox>&ndash;&gt;-->
<!--            </el-popover>-->
          </el-button-group>
        </div>
      </div>
      <div class="app-container-item">
        <TableBase v-bind="tableOptions" ref="table"></TableBase>
      </div>
      <el-dialog
        title="用户编辑"
        :visible.sync="centerDialogVisible"
        width="480px"
        >
        <el-form :model="userObj" :rules="rules" label-suffix="" ref="ruleForm" label-width="101px" class="demo-ruleForm">
          <el-form-item label="用户名" prop="userName">
            <el-input v-model="userObj.userName" placeholder="用户名" :disabled="!addOrModifyMark"></el-input>
          </el-form-item>
          <el-form-item label="姓名" prop="realName">
            <el-input v-model="userObj.realName" placeholder="姓名" ></el-input>
          </el-form-item>
          <el-form-item label="盐" prop="salt">
            <el-input v-model="userObj.salt" placeholder="盐" :disabled="!addOrModifyMark"></el-input>
          </el-form-item>
          <el-form-item label="性别" prop="sex">
            <el-radio v-model="userObj.sex" label="0">男</el-radio>
            <el-radio v-model="userObj.sex" label="1">女</el-radio>
          </el-form-item>
          <el-form-item label="电话" prop="phone">
            <el-input v-model="userObj.phone" placeholder="电话" ></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="userObj.email" placeholder="邮箱" ></el-input>
          </el-form-item>
          <el-form-item label="地区" prop="areaCode">
            <Divisions :divisionValue="divisionValue" @sendDivisions="getDivisions"></Divisions>
          </el-form-item>
          <el-form-item label="角色" prop="sysRoleIds">
            <el-cascader
              v-model="roleValue"
              :props="roleProp"
              :options="options"
              filterable>
            </el-cascader>
          </el-form-item>
          <el-form-item label="账户状态" prop="status">
            <el-radio v-model="userObj.status" label="0">初始化</el-radio>
            <el-radio v-model="userObj.status" label="1">正常</el-radio>
            <el-radio v-model="userObj.status" label="2">停用</el-radio>
            <el-radio v-model="userObj.status" label="3">锁定</el-radio>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button type="text" @click="centerDialogVisible = false" >取 消</el-button>
        <el-button type="primary" @click="saveUserObj" >确 定</el-button>
      </span>
      </el-dialog>
      <router-view></router-view>
    </div>
</template>

<script>
import TableBase from '@/components/table/TableBase'
import Divisions from '@/components/divisions/Divisions'
import {queryUserList, queryRoleTree, addOrModifyUser, queryUser, delUserList, initPwd} from '@/api'
export default {
  name: 'User',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 查询是否显示
      showQuery: true,
      divisionValue: [],
      roleValue: [],
      options: [],
      // 编辑弹框显示隐藏
      centerDialogVisible: false,
      // 用来区别是新增还是修改  新增是true 修改是false
      addOrModifyMark: false,
      // 弹框绑定的数据对象
      userObj: {},
      roleProp: {
        multiple: true,
        expandTrigger: 'hover',
        value: 'id',
        label: 'name',
        children: 'roleList'
      },
      // 填写验证
      rules: {
        userName: [{ required: true, message: '用户名称不能为空', trigger: 'blur' }],
        realName: [{ required: true, message: '真实姓名不能为空', trigger: 'blur' }],
        salt: [{ required: true, message: '盐不能为空', trigger: 'blur' }],
        status: [{ required: true, message: '账号状态不能为空', trigger: 'blur' }]
      },
      tableOptions: {
        api: queryUserList,
        query: {
          userName: ''
        },
        hideOnSinglePage: false,
        staticData: [],
        columns: [
          {
            attr: {
              prop: 'checkbox',
              type: 'selection',
              label: '多选',
              width: '80'
            }
          },
          {
            attr: {
              type: 'index',
              label: '序号',
              width: '80'
            }
          },
          {
            attr: {
              prop: 'userName',
              label: '用户名'
            }
          },
          {
            attr: {
              prop: 'realName',
              label: '姓名'
            }
          },
          {
            attr: {
              prop: 'salt',
              label: '盐',
              'show-overflow-tooltip': true
            }
          },
          {
            attr: {
              prop: 'sex',
              label: '性别',
              formatter (row, column, cellValue, index) {
                let val = '男'
                if (cellValue === '1') {
                  val = '女'
                }
                return val
              }
            }
          },
          {
            attr: {
              prop: 'phone',
              label: '电话'
            }
          },
          {
            attr: {
              prop: 'email',
              label: '邮箱'
            }
          },
          {
            attr: {
              prop: 'userType',
              label: '用户类型',
              formatter (row, column, cellValue, index) {
                let val = '内部人员'
                return val
              }
            }
          },
          {
            attr: {
              prop: 'status',
              label: '状态',
              formatter (row, column, cellValue, index) {
                let val = '初始化'
                if (cellValue === '1') {
                  val = '正常'
                } else if (cellValue === '2') {
                  val = '停用'
                } else if (cellValue === '3') {
                  val = '锁定'
                }
                return val
              }
            }
          },
          {
            attr: {
              prop: 'operation',
              label: '操作',
              width: 100,
              fixed: 'right'
            },
            template: [
              {
                el: 'el-button',
                attr: {
                  type: 'text'
                },
                content: '修改',
                attrByRow: {},
                handle: (index, row) => {
                  this.modifyUser(row)
                }
              }, {
                el: 'el-button',
                attr: {
                  type: 'text'
                },
                content: '删除',
                attrByRow: {},
                handle: (index, row) => {
                  this.removeUser(row)
                }
              }
            ]
          }
        ]
      }
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {TableBase, Divisions},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
    // 加载角色
    this.selectRole()
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /** 获取编码数组 */
    getDivisions (divisions) {
      this.divisionValue = divisions
    },
    /**
     * 查询表格数据
     */
    loadData () {
      this.$refs.table.refresh()
    },
    /**
     * 重置按钮点击事件
     */
    clear () {
      this.tableOptions.query = {
        userName: ''
      }
    },
    /**
     * @desc 控制查询的显隐
     */
    toggleQuery () {
      this.showQuery = !this.showQuery
    },
    /**
     * @desc 刷新表格
     */
    refresh () {
      this.$refs.table.refresh()
    },
    /**
     * 加载角色列表
     */
    selectRole () {
      queryRoleTree({}).then((response) => {
        if (response.data.code === 0) {
          this.options = response.data.data
        }
      })
    },
    addUser () {
      this.centerDialogVisible = true
      this.addOrModifyMark = true
      this.userObj = {
        userName: '',
        realName: '',
        phone: '',
        email: '',
        sex: '0',
        areaCode: '',
        sysRoleIds: [],
        status: '0',
        salt: this.$common.getUUID().replace(/-/g, ''),
        userType: '0',
        delFlag: '0'
      }
      this.divisionValue = []
      this.roleValue = []
    },
    saveUserObj () {
      this.$refs.ruleForm.validate((valid) => {
        if (!valid) {
          return false
        }
        const loadObj = this.$loading({
          lock: true,
          text: '正在加载...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        let url = ''
        if (this.addOrModifyMark) {
          url = 'user/add'
        } else {
          url = 'user/modify'
        }
        let areaCode = ''
        this.divisionValue.forEach((val, index) => {
          if (index !== this.divisionValue.length - 1) {
            areaCode += val + '_'
          } else {
            areaCode += val
          }
        })
        this.userObj.areaCode = areaCode

        let sysRoleIds = []
        this.roleValue.forEach(val => {
          sysRoleIds.push(val[0] + '_' + val[1])
        })
        this.userObj.sysRoleIds = sysRoleIds
        addOrModifyUser(url, this.userObj).then((response) => {
          loadObj.close()
          if (response.data.code === 0) {
            this.$message({
              showClose: true,
              message: '保存成功！',
              type: 'success'
            })
            this.centerDialogVisible = false
            this.loadData()
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: 'error'
            })
          }
        }).catch((response) => {
          loadObj.close()
          this.centerDialogVisible = false
          this.$message({
            showClose: true,
            message: '保存失败！',
            type: 'error'
          })
        })
      })
    },
    modifyUser (row) {
      if (row.type === 'click') {
        if (this.$refs.table.getSelectData().length !== 1) {
          this.$message({
            showClose: true,
            message: '请选择一条数据修改！',
            type: 'warning'
          })
          return
        }
        row = this.$refs.table.getSelectData()[0]
      }
      queryUser({id: row.id}).then((response) => {
        if (response.data.code === 0) {
          this.userObj = response.data.data
          this.divisionValue = response.data.data.areaCode.split('_')
          let roleValue = []
          response.data.data.sysRoleIds.forEach(item => {
            let arr = []
            arr.push(item.split('_')[0])
            arr.push(item.split('_')[1])
            roleValue.push(arr)
          })
          this.roleValue = roleValue
        } else {
          this.$message({
            showClose: true,
            message: response.data.message,
            type: 'error'
          })
        }
      })
      this.centerDialogVisible = true
      this.addOrModifyMark = false
    },
    removeUser (row) {
      let delList = []
      if (row.type === 'click') {
        if (this.$refs.table.getSelectData().length < 1) {
          this.$message({
            showClose: true,
            message: '请至少选择一条数据删除！',
            type: 'warning'
          })
          return
        }
        delList = this.$refs.table.getSelectData()
      } else {
        delList.push(row)
      }
      this.$confirm('确认是否删除角色，此操作无法撤销?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 执行删除操作
        let userPo = {
          listUserPo: []
        }
        delList.forEach((oneObj) => {
          let delObj = {
            id: oneObj.id,
            version: oneObj.version
          }
          userPo.listUserPo.push(delObj)
        })

        delUserList(userPo).then((response) => {
          if (response.data.code === 0) {
            this.$message({
              showClose: true,
              message: '删除成功！',
              type: 'success'
            })
            this.$refs.table.refresh()
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: 'error'
            })
          }
        }).catch((response) => {
          this.$message({
            showClose: true,
            message: '删除失败！',
            type: 'error'
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    initPwd (row) {
      let delList = []
      if (row.type === 'click') {
        if (this.$refs.table.getSelectData().length < 1) {
          this.$message({
            showClose: true,
            message: '请至少选择一条数据！',
            type: 'warning'
          })
          return
        }
        delList = this.$refs.table.getSelectData()
      } else {
        delList.push(row)
      }
      this.$confirm('确认是否初始化密码，此操作无法撤销?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 执行初始化密码操作
        let userPo = {
          listUserPo: []
        }
        delList.forEach((oneObj) => {
          let delObj = {
            id: oneObj.id,
            version: oneObj.version,
            salt: oneObj.salt
          }
          userPo.listUserPo.push(delObj)
        })
        initPwd(userPo).then((response) => {
          if (response.data.code === 0) {
            this.$message({
              showClose: true,
              message: '初始化密码成功！',
              type: 'success'
            })
            this.$refs.table.refresh()
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: 'error'
            })
          }
        }).catch((response) => {
          this.$message({
            showClose: true,
            message: '初始化密码失败！',
            type: 'error'
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消操作'
        })
      })
    }
  }
}
</script>

<style scoped>

</style>



